













import { Component, Vue } from 'vue-property-decorator';
import gsap from 'gsap';

@Component
export default class Picture extends Vue{
  $refs!: {
    vid: HTMLVideoElement;
    can: HTMLCanvasElement;
  }

  pictureCnt = 3;
  vid?: HTMLVideoElement;
  ctx?: CanvasRenderingContext2D;
  can?: HTMLCanvasElement;
  req?: any;
  interval?: any;
  localStream?: MediaStream;
  error = "";

  mounted(){
    this.vid = this.$refs.vid;
    this.can = this.$refs.can;
    this.can.width = 500;
    this.can.height = 500;

    this.ctx = this.can.getContext('2d');

    navigator.mediaDevices.getUserMedia({video: {width: 500, height: 500}})
    .then((stream) => {
      this.localStream = stream;
      this.vid.srcObject = stream;

      this.interval = setInterval(()=>{
        if(this.pictureCnt > 0){
          this.pictureCnt --;
        } else {
          this.takePicture();
        }
      }, 1000);

      this.req = this.update();
    })
    .catch((error: Error) => {
      this.error = "Webcam not found";
      this.$emit('handleError');
    });

  }

  private update() {
    this.ctx.drawImage(this.vid, 0, 0);

    this.req = window.requestAnimationFrame(this.update);
  }

  private takePicture(){
    this.vid.pause();
    document.getElementById('flash').style.opacity = '1';
    gsap.to("#flash", .3, {opacity: 0});
    clearInterval(this.interval);
    window.cancelAnimationFrame(this.req);
    setTimeout(()=>{
      this.$emit('pictureTaken', this.can.toDataURL('image/jpeg'));
    }, 3000);
  }

  beforeDestroy(){
    this.vid.src = "";
    if(this.localStream){
      this.localStream.getTracks()[0].stop();
    }
  }

}
